import React from 'react';

import Item from './Item';
import Footer from './Footer';

class Main extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      currentToDo: "",
      todos: [
        // {
        //   id: 1,
        //   text: 1,
        //   completed: false
        // }
      ],
      selectedFilter: "All",
      finalTodos: [],
      counterActiveTodos: 0,
    }

  };

  handleChange = async (event) => {
    const { value } = event.target;

    await this.setState({
      currentToDo: value
    });
    console.log(value);
  }

  handleKeyPress = async (event) => {
    if (event.which === 13) {
      event.preventDefault();
      let key = require("randomstring");
      const counterActiveTodos = this.state.counterActiveTodos;
      key = key.generate({
        length: 5,
        charset: 'alphabetic'
      });
      console.log(key);
      const initToDo = [...this.state.todos, {
        id: key,
        text: this.state.currentToDo,
        completed: false
      }]
      await this.setState({
        todos: initToDo,
        currentToDo: "",
        counterActiveTodos: counterActiveTodos + 1,
      });
      // console.log(this.state.currentToDo);
      console.log(this.state.todos);
    };
  }

  handleDelete = (id) => {
    
    const counterActiveTodos = this.state.counterActiveTodos,
      todos = [...this.state.todos],
      upDateTodos = todos.filter((curr) => curr.id !== id);
    this.setState({ todos: upDateTodos });
    if (todos.some(el => (el.id === id)&(!el.completed))) {
      this.setState({ counterActiveTodos: counterActiveTodos - 1})
    }
  }
  
  handleChangeStatus = async (event) => {
    console.log(event.target.id);
    // const todos = this.state.todos.map(el => {
      //   if  (el.id === event.target.id) {
        //     if (!el.completed){
          //       el.completed = true;
          //     } else {
    //       el.completed = false;
    //     }
    //   }
    //   return el;
    // });
    const todos = [...this.state.todos];
    const counterActiveTodos = this.state.counterActiveTodos;
    todos[event.target.id]['completed'] = event.target.checked;
    if (event.target.checked) {
      await this.setState({
        todos: todos,
        counterActiveTodos: counterActiveTodos - 1,
      })
    } else {
      await this.setState({
        todos: todos,
        counterActiveTodos: counterActiveTodos + 1,
      })
    }
    console.log(todos);
  }
  
  filter = async () => {
    let finalTodoss;
    const todos = [...this.state.todos];
    
    if (this.state.selectedFilter === "Active") {
      finalTodoss = todos.filter((elem) => (elem.completed === false));
      console.log(this.state.selectedFilter);
      console.log(todos)
    } else /* (this.selectedFilter === "Completed") */ {
      finalTodoss = todos.filter((elem) => (elem.completed === true));
      console.log(this.finalTodoss);
    } /* else {
      finalTodoss = todos;
      console.log(this.state.selectedFilter);
    }; */

    await this.setState({
      finalTodos: finalTodoss
    })
    console.log(this.state.finalTodos)
  }

  selectFilter = async (event) => {
    await this.setState({
      selectedFilter: event.target.name
    });
    this.filter();
  }


  render() {  
    return (
      <>
        <div className="todos">
          <input
            type="text"
            value={this.state.currentToDo}
            placeholder="What need to be done?"
            onChange={this.handleChange}
            onKeyPress={this.handleKeyPress}
          />
          <div className="todo-list">
            <ul>
              {this.state.todos.map((elem, i) => (
                <Item 
                  id={elem.id}
                  text={elem.text}
                  checked={elem.completed}
                  onChange={this.handleChangeStatus}
                  i={i}
                  handleDelete={this.handleDelete}
                />
              ))}
            </ul>
          </div>
          {(this.state.todos.length > 0) ? 
            (<Footer
            selected={this.state.selectedFilter}
            counter={this.state.counterActiveTodos}
            length={this.state.todos.length}
            onclick={this.selectFilter}
          />) : ""
          }
          
        </div>
        
      </>
    )
  }
}

export default Main;